@import url(https://fonts.googleapis.com/css2?family=Island+Moments&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);

:root {
  --primary-color: #ff7e01;
  --secondary-color: #faa935;
  --heading-color: #0b2727;
  --text-color: #6e7074;
  --font-name: "Montserrat", sans-serif;
  --subtitle-font-name: "Island Moments", cursive;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-name);
}

body::-webkit-scrollbar {
  display: none;
}

/* section {
  padding: 40px 0px;
} */

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--heading-color);
}

a {
  text-decoration: none;
  color: unset;
}

ul {
  list-style: none;
}

.primary__btn {
  background: var(--secondary-color) !important;
  border: none !important;
  border-radius: 50px !important;
  padding: 0.4rem 1.5rem !important;
}

.primary__btn a {
  font-size: 1.1rem;
  text-decoration: none;
  color: #fff;
}

.primary__btn a:hover {
  color: inherit;
}

.secondary__btn {
  background: transparent !important;
  color: #0b2727 !important;
  border: none !important;
  font-weight: 500 !important;
}

.secondary__btn a {
  text-decoration: none;
  font-size: 1.1rem;
  color: var(--heading-color);
}

.secondary__btn a:hover {
  color: inherit;
}

.section__subtitle {
  background: var(--secondary-color);
  font-family: var(--subtitle-font-name);
  width: max-content;
  padding: 0px 0.5rem;
  padding-right: 1rem;

  border-radius: 50px;
  font-weight: 500;
  font-size: 1.7rem;
  color: var(--heading-color);
}


@media only screen and (max-width:992px) {

  .primary__btn,
  .secondary__btn {
    font-size: 0.9rem;
  }

  .primary__btn,
  .secondary__btn,
  .btn-dark {
    padding: 0.2rem 1.1rem !important;
  }

  section {
    padding: 40px 0;
  }
}

@media only screen and (max-width:992px) {
  .section__subtitle {
    font-size: 1.3rem;
  }
}
.header {
    width: 100%;
    height: 80px;
    line-height: 80px; 
}

.logo img {
    width: 22%;
}

.menu {
    margin-bottom: 0;
    margin-right: 8.5rem; 
    display: flex;
}

.nav__item a {
    text-decoration: none;
    color: var(--heading-color); 
    font-size: 1.4rem;
}

.nav__item a:hover {
    color: inherit; 
}

.nav__item a.active__link {
    color: var(--secondary-color); 
}

.mobile__menu {
    font-size: 1.3rem;
    color: var(--heading-color); 
    display: none; 
}

.sticky__header {
    width: 100%;
    height: 80px;
    line-height: 80px; 
    background: #fff; 
    position: sticky;
    top: 0;
    left: 0;
    box-shadow: 3px 3px 8px -3px #ddd; 
    z-index: 999; 
}

/* Media Queries */
@media only screen and (max-width: 992px) {
    .logo img {
        width: 20%; 
    }
    .navigation {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.411); 
        z-index: 999;
    }
    .menu {
        position: absolute;
        top: 0;
        right: 0;
        width: calc(100% - 75%);
        height: 100%;
        background: #fff; 
        flex-direction: column; 
        justify-content: center; 
        line-height: 0.5rem; 
        z-index: 9999; 
    }
    .show__menu {
        display: none; 
    }
    .mobile__menu {
        display: block; 
    }
}

@media only screen and (max-width: 768px) {
    .logo img {
        width: 45%; 
    }
    .menu {
        width: calc(100% - 50%); 
    }
}

.main {
    padding: 3.5rem 0;
}

.main__content, 
.main__content h1 {
    font-size: 3.2rem;
    font-weight: 600;
}

.main__content h1 .highlight {
    color: var(--secondary-color); 
}

.main__content p,
.experience__content p {
    font-size: 1.1rem;
    color: var(--text-color); 
    line-height: 2rem; 
    margin-top: 1rem;
}

.main__img-box img,
.main__img-box video {
    width: 100%;
    height: 350px;
    border-radius: 20px;
    border: 1px solid var(--secondary-color); 
    object-fit: cover; 
}

.main__img-box {
    padding-top: 5rem;
}

.subtitle,
.gallery__title,
.services__title,
.testimonial__title,
.experience__content h2 {
    font-size: 3.5rem;
    color: #ee6e6e; 
    font-family: var(--subtitle-font-name); 
}

.gallery__title,
.testimonial__title {
    font-size: 2.3rem;
    margin: 1rem 0 2.5rem;
}

.counter__box span {
    width: 70px;
    height: 70px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--primary-color);
    color: #fff;
    font-size: 1.6rem;
    font-weight: 600;
    border-radius: 10px 5px 10px 5px;
}

.counter__box h6 {
    font-size: 1.1rem;
    margin-top: 0.7rem;
    color: var(--text-color);
}

.experience__img img {
    max-width: 90%;
    height: auto;
    object-fit: contain;
    margin-left: 4rem;
}

.mesonry__img {
    transition: transform 0.3s; 
}

.mesonry__img:hover {
    transform: scale(1.02);
}

.main__subtitle img {
    width: 2.3rem;
    height: 2.3rem;
}

/* Media Queries */
@media only screen and (max-width: 992px) {
    .main__content h1,
    .services__title,
    .gallery__title,
    .testimonial__title,
    .experience__content h2 {
        font-size: 2rem;
    }

    .main__img-box {
        display: none; 
    }

    .main__video-box {
        display: block; 
    }

    .experience__img {
        margin-top: 2rem; 
    }
}

@media only screen and (max-width: 576px) {
    .main__content h1,
    .services__title,
    .gallery__title,
    .testimonial__title,
    .experience__content h2 {
        font-size: 1.5rem; 
    }

}

.service__item{
    padding: 1rem;
    border-bottom: 1px solid #faa83586;
    border-radius: 1px solid #faa83586;
    border-radius: 0.5rem;

}



.service__img{
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    background: var(--secondary-color);
    padding: 0.5rem;
    margin-bottom: 1rem;
    line-height: 50px;
}

.service__img img{
    width: 100%;
}

.service__item h5{
    font-size: 1.1rem;
}

.service__item p{
    font-size: .9rem;
    color: var(--text-color);
}
.tour__img img{
    width: 100%;
    border-radius: 5px 5px 0 0;
}

.card{
    border: none !important;
    box-shadow: rgba(0,0,0,0.2) 0px 18px 50px -10px;
}

.tour__img{
    position: relative;
}

.tour__img span{
    position: absolute;
    bottom: 0;
    right: 0;
    width: max-content;
    height: max-content;
    z-index: 10;
    background: var(--primary-color);
    color: #fff;
    padding: 0.3rem 0.5rem;
    border-radius: 3px 0 0 0 ;
}

.card__top span i{
    color: var(--secondary-color);
}

.tour__rating{
    font-size: 0.9rem;
    color: var(--text-color);
}

.tour__location{
    color: var(--heading-color);
    font-weight: 500;
}

.tour__rating i,
.tour__location i{
    font-size: 1rem;
}

.tour__title{
    margin-top: 1rem;
    font-size: 1.1rem;
    cursor: pointer;
}

.tour__title a{
    text-decoration: none;
    color: var(--heading-color);
    transform: .3s;
}

.tour__title a:hover{
    color: var(--secondary-color);
}

.card__bottom h5{
    color: var(--secondary-color);
    font-size: 1.1rem;
    font-weight: 700;
}

.card__bottom h5 span{
    font-weight: 500;
    color: var(--text-color);
    font-size: 0.8rem;
}

.booking__btn{
    background: var(--secondary-color) !important;
    cursor: pointer;
    padding: 0.2rem 0.5rem !important;
}

.booking__btn a{
    text-decoration: none;
    font-size: 0.9rem;
    color: #fff;
}

.booking__btn a:hover{
    color: #fff;
}

@media only screen and (max-width:576) {
    .tour__title{
        font-size: 1rem;
    }
    .booking__btn{
        font-size: 0.8rem;
    }
    .booking__btn{
        padding: 0.2rem 0.5rem !important;
    }
    .tour__location,.tour__rating{
        font-size: .8rem;
    }
}


.login__img {
    width: 60%;
    text-align: center;
}

.login__img img {
    width: 70%;
    object-fit: contain; 
}

.login__form {
    padding: 50px;
    background: var(--secondary-color); 
    position: relative;
}

.login__container {
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
    margin-top: 5%;
}

.login__form .user {
    width: 80px;
    height: 80px;
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translate(-50%, -20%); 
}

.login__form .user img {
    width: 100%;
}

.login__form h2 {
    color: #fff;
    text-align: center;
    margin-bottom: 2rem;
}

.login__form {
    width: 40%;
}

.login__form input {
    width: 100%;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    border: none;
    font-size: 1rem;
    color: var(--heading-color); 
}

.login__form input:focus {
    outline: none;
}

.auth__btn {
    background: var(--heading-color)!important; 
    width: 100%;
    color: #fff !important;
}

.login__form p {
    font-size: .9rem;
    color: #fff;
    margin-top: 1.5rem;
    text-align: center;
}

.login__form p a {
    color: var(--heading-color);
    text-decoration: none;
    margin-left: 5px;
    font-weight: 500;
}

@media only screen and (max-width: 768px) {
    .login__img {
        display: none; 
    }
    .login__form {
        width: 70%;
        margin: auto; 
    }
    .login__container {
        padding-bottom: 3rem;
        margin-top: 70px;
    }
}

@media only screen and (max-width: 576px) {
    .login__form {
        width: 100%; 
    }
    .login__container {
        padding-bottom: 1rem;
    }
}

.common__section{
    background: linear-gradient(rgba(0,0,0,0.45),rgba(0,0,0,0.45)),url(/static/media/tour.4cc61ea0.jpg)no-repeat center center;
    background-size: cover;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.common__section h1{    
    font-size: 2.5rem;
    color: #fff;
}
.tour__content img{
    width: 100%;
    border-radius: 0.5rem;
    margin-bottom: 2.5rem;
}

.tour__info,.tour__reviews{
    border-radius: 0.5rem;
    border: 1px solid rgb(229, 221, 235);
    padding: 2rem;
}

.tour__info h2{
    font-size: 1.5rem;
}

.tour__info span{
    display: flex;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    font-size: 1rem;
    color: var(--text-color);
}

.tour__info span i{
    color: var(--heading-color);
    font-size: 1.1rem;
}

.tour__extra-details{
    display: flex;
    align-items: center;
    grid-column-gap: 2.7rem;
    column-gap: 2.7rem;
    margin-top: 1rem;
    margin-bottom: 2.5rem;
}

.tour__info h5{
    margin-bottom: 30px;
    font-size: 1.3rem;
}

.tour__info p{
    line-height: 30px;
    font-size: 1rem;
    color: var(--text-color);
}

.rating__group span{
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    color: var(--secondary-color);
    cursor: pointer;
}

.tour__reviews h4{
    margin-bottom: 3rem;
}

.review__input{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0.7rem;
    border-radius: 2rem;
    border: 1px solid var(--secondary-color);
}

.review__input input{
    width: 100%;
    padding: 0.5rem 0;
    border: none;
}

.review__input input:focus{
    outline: none;
}

.review__item{
    display: flex;
    align-items: center;
    grid-column-gap: 1rem;
    column-gap: 1rem;
    margin-bottom: 2rem;
}

.review__item img{
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50px;
    object-fit: cover;
}


.user__reviews{
    margin-top: 2.5rem;

}

.review__item h5{
    font-size: 1rem;
    margin-bottom: 0;
}

.review__item p{
    font-size: .9rem;
    color: var(--text-color);
}

.review__item p{
    font-size: 1.1rem;
    color: var(--text-color);
}
.review__item span i{
    font-size: 1.2rem;
    color: var(--secondary-color);
}

.review__item span{
    font-weight: 500;
}

@media only screen and (max-width:768px){
    .tour__extra-details{
        flex-wrap: wrap;
        grid-row-gap: 1rem;
        row-gap: 1rem;
    }

    .tour__extra-details span{
        font-size: .9rem;
    }

    .tour__extra-details span i{
        font-size: 1rem;
    }
}




.booking {
    padding: 2rem;
    border-radius: 0.5rem;
    border: 1px solid rgb(229, 231, 235);
    position: sticky;
    top: 80px;
}

.booking__top h3 {
    font-size: 2rem;
    font-weight: 700;
}

.booking__top h3 span {
    font-size: 1.1rem;
    font-weight: 400;
    color: var(--text-color);
}

.booking__top span {
    color: var(--secondary-color);

}

.booking__top {
    padding-bottom: 2rem;
    border-bottom: 1px solid rgb(229, 231, 235);
}

.booking__form h5 {
    margin-bottom: 1rem;
}

.booking__form input {
    width: 100%;
    padding: 0.5rem;
    border-radius: .5rem;
