    color: var(--text-color);
    font-weight: 500;
}

.booking__bottom {
    margin-top: 1rem;
}

.total h5,
.total span {
    color: var(--heading-color) !important;
    font-weight: 700 !important;
    font-size: 1.1rem !important;
}
.pagination span{
    width: 2rem;
    height: 2rem;
    padding: 5px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--secondary-color);
    color: var(--heading-color);
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 700;
}
.active__page{
    background: var(--secondary-color);
    color: #fff !important;
}
