}

.footer .logo img {
    width: 50%;
}

.company, .links, .contacts {
    padding-top: 1rem;
}

.company {
    margin-left: 5rem;
}

.links {
    margin-left: 2rem;
}

.quick-links li a, .contact-links li a {
    text-decoration: none;
    color: var(--text-color);
    font-size: 1.2rem;
}

.socials span a {
    text-decoration: none;
    color: var(--heading-color);
}

